<template>
  <div>
    <el-button type="primary" size="small" @click="() => appendFirst(data)">
      添加一级菜单
    </el-button>
    <el-tree
      :data="data"
      :props="defaultProps"
      node-key="path"
      default-expand-all
      :expand-on-click-node="false"
    >
      <div class="custom-tree-node" slot-scope="{ node, data }">
        <span><i :class="data.icon"></i>{{ node.label }} </span>
        <span>
          <el-button
            type="primary"
            size="small"
            v-if="node.level < 3"
            @click="() => append(node, data)"
          >
            添加
          </el-button>
          <el-button type="warning" size="small" @click="() => editNode(data)">
            编辑
          </el-button>
          <el-button type="danger" size="small" @click="() => removeNode(data)">
            删除
          </el-button>
        </span>
      </div></el-tree
    >
    <el-dialog
      title="菜单"
      :visible.sync="visible"
      :rules="rules"
      width="300px"
    >
      <el-form :model="form" label-position="right" label-width="60px">
        <el-form-item label="菜单名" prop="menu_name">
          <el-input size="small" v-model="form.menu_name"></el-input>
        </el-form-item>
        <el-form-item label="路径" prop="menu_path">
          <el-input size="small" v-model="form.menu_path"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon">
          <el-input size="small" v-model="form.icon"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="visible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="createNode"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  label: "menu",
  data() {
    return {
      defaultProps: {
        children: "sub_tree",
        label: "menu_name",
      },
      rules: {
        menu_name: [{ required: true, message: "不可为空", trigger: "blur" }],
        menu_path: [{ required: true, message: "不可为空", trigger: "blur" }],
        icon: [{ required: true, message: "不可为空", trigger: "blur" }],
      },
      visible: false,
      labelPosition: "right",
      form: {},
      data: [],
      level: "",
    };
  },
  mounted() {
    this.getMenuList();
  },
  methods: {
    getMenuList() {
      this.$api.getMenu().then((res) => {
        this.data = res.data.menu_list;
      });
    },
    // 创建一级菜单
    appendFirst() {
      this.visible = true;
    },
    createNode() {
      if (this.form.menu_code) {
        // 修改菜单
        this.$api.editMenu(this.form).then((res) => {
          this.visible = false;
          this.form = {};
          this.getMenuList();
        });
      } else {
        // 新增菜单
        this.$api.createMenu(this.form).then((res) => {
          this.visible = false;
          this.form = {};
          this.getMenuList();
        });
      }
    },
    // 添加下一级节点
    append(node, data) {
      console.log(node.level);
      this.visible = true;
      this.level = node.level;
      this.$set(this.form, "super_code", data.menu_code);
    },
    editNode(data) {
      console.log(data);
      this.visible = true;
      this.form = data;
    },
    removeNode(data) {
      this.$confirm("确定删除菜单项？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      }).then(() => {
        this.$api
          .deleteMenu({
            menu_code: data.menu_code,
          })
          .then((res) => {
            console.log(res);
            this.getMenuList();
          });
      });
    },
  },
};
</script>

<style>
.custom-tree-node {
  height: 40px;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
  padding: 10px 0;
}
</style>



