<template>
  <el-row class="container">
    <el-col :span="24" class="header">
      <el-col
        :span="10"
        class="logo"
        :class="collapsed ? 'logo-collapse-width' : 'logo-width'"
      >
        {{ collapsed ? "" : sysName }}
      </el-col>
      <el-col :span="10">
        <div class="tools" @click.prevent="collapse" style="display: none">
          <i class="fa fa-align-justify"></i>
        </div>
        <el-button
          type="primary"
          size="small"
          icon="el-icon-arrow-left"
          @click="$router.back(-1)"
        >
          返回
        </el-button>
      </el-col>
      <el-col :span="4" class="userinfo">
        <span class="el-dropdown-link userinfo-inner">
          <!-- <img :src="this.sysUserAvatar" />-->
          <icon
            class="el-icon-user-solid"
            size="26"
            style="margin-right: 5px"
          ></icon
          >{{ sysUserName }}</span
        >
        <el-button type="primary" icon="el-icon-switch-button" @click="logout"
          >退出登录</el-button
        >
      </el-col>
    </el-col>
    <el-col :span="24" class="main">
      <aside
        :class="collapsed ? 'menu-collapsed' : 'menu-expanded'"
        style="overflow-y: scroll"
      >
        <!--导航菜单-->
        <el-menu
          :default-active="currentChoose"
          class="el-menu-vertical-demo"
          unique-opened
          router
          v-show="!collapsed"
        >
          <template v-for="(item, index) in routerList">
            <el-submenu :index="index + ''" v-if="!item.leaf">
              <template slot="title"
                ><i :class="item.iconCls"></i>{{ item.name }}</template
              >
              <el-menu-item
                v-for="child in item.children"
                :index="child.path"
                :key="child.path"
                v-if="!child.hidden"
                ><i :class="child.iconCls"></i>{{ child.name }}</el-menu-item
              >
            </el-submenu>
            <el-menu-item
              v-if="item.leaf"
              :key="item.children[0].path"
              :index="item.children[0].path"
              ><i :class="item.iconCls"></i
              >{{ item.children[0].name }}</el-menu-item
            >
          </template>
        </el-menu>
        <!--导航菜单-折叠后-->
        <ul
          class="el-menu el-menu-vertical-demo collapsed"
          v-show="collapsed"
          ref="menuCollapsed"
        >
          <li
            v-for="(item, index) in routerList"
            :key="index"
            class="el-submenu item"
          >
            <template v-if="!item.leaf">
              <div
                class="el-submenu__title"
                style="padding-left: 20px"
                @mouseover="showMenu(index, true)"
                @mouseout="showMenu(index, false)"
              >
                <i :class="item.iconCls"></i>
              </div>
              <ul
                class="el-menu submenu"
                :class="'submenu-hook-' + index"
                @mouseover="showMenu(index, true)"
                @mouseout="showMenu(index, false)"
              >
                <li
                  v-for="child in item.children"
                  v-if="!child.hidden"
                  :key="child.path"
                  class="el-menu-item"
                  style="padding-left: 40px"
                  :class="currentChoose == child.path ? 'is-active' : ''"
                  @click="$router.push(child.path)"
                >
                  {{ child.name }}
                </li>
              </ul>
            </template>
            <template v-else>
              <li class="el-submenu">
                <div
                  class="el-submenu__title el-menu-item"
                  style="
                    padding-left: 20px;
                    height: 56px;
                    line-height: 56px;
                    padding: 0 20px;
                  "
                  :class="
                    currentChoose == item.children[0].path ? 'is-active' : ''
                  "
                  @click="$router.push(item.children[0].path)"
                >
                  <i :class="item.iconCls"></i>
                </div>
              </li>
            </template>
          </li>
        </ul>
      </aside>
      <section class="content-container">
        <div class="grid-content bg-purple-light">
          <el-col
            :span="24"
            class="breadcrumb-container"
            style="margin-bottom: 20px"
          >
            <strong class="title">{{ $route.name }}</strong>
            <el-breadcrumb separator="/" class="breadcrumb-inner">
              <el-breadcrumb-item
                v-for="item in $route.matched"
                :key="item.path"
              >
                {{ item.name }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </el-col>
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <!-- <keep-alive include="operateRecord"> -->
              <router-view></router-view>
              <!-- </keep-alive> -->
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      sysName: "MFP管理后台",
      collapsed: false,
      currentChoose: "",
      sysUserName: "",
      sysUserAvatar: "",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
    };
  },
  computed: {
    routerList() {
      return this.$router.options.routes.filter((item) => {
        return !item.hidden;
      });
    },
  },
  // created() {
  //   window.addEventListener("beforeunload", (e) => {
  //     var dialogText = "要重新加载该网站吗？可能需要重新登录";
  //     e.returnValue = dialogText;
  //     return dialogText;
  //   });
  // },
  mounted() {
    var user = sessionStorage.getItem("user");
    if (user) {
      user = JSON.parse(user);
      this.sysUserName = user.name || user.shop_name;
      this.sysUserAvatar = user.avatar || "";
    }
  },
  watch: {
    $route: function (obj) {
      if (!obj.meta.flag) {
        this.currentChoose = obj.path;
      }
    },
  },
  methods: {
    // beforeLoad(event) {
    //   console.log(this.$router.options.routes);
    //   console.log(event);
    //   debugger;
    //   this.$confirm("刷新页面可能导致登录态丢失，确定刷新？", "提示", {}).then(
    //     () => {
    //       debugger;
    //       // next();
    //     }
    //   );
    // },
    //退出登录
    logout: function () {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {}).then(() => {
        sessionStorage.removeItem("user");
        sessionStorage.removeItem("route");
        _this.$router.push("/login");
        location.reload();
      });
    },
    //折叠导航栏
    collapse: function () {
      this.collapsed = !this.collapsed;
    },
    showMenu(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    },
  },
  // destroyed() {
  //   window.removeEventListener("beforeunload");
  // },
};
</script>

<style scoped lang="scss">
@import "~scss_vars";

.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .header {
    height: 60px;
    line-height: 60px;
    background: $color-primary;
    color: #fff;
    .userinfo {
      text-align: right;
      padding-right: 15px;
      float: right;
      .userinfo-inner {
        cursor: pointer;
        color: #fff;
        img {
          width: 40px;
          height: 40px;
          border-radius: 20px;
          margin: 10px 0px 10px 10px;
          float: right;
        }
      }
    }
    .logo {
      //width:230px;
      height: 60px;
      font-size: 22px;
      padding-left: 20px;
      padding-right: 20px;
      border-color: rgba(238, 241, 146, 0.3);
      border-right-width: 1px;
      border-right-style: solid;
      img {
        width: 40px;
        float: left;
        margin: 10px 10px 10px 18px;
      }
      .txt {
        color: #fff;
      }
    }
    .logo-width {
      width: 230px;
    }
    .logo-collapse-width {
      width: 60px;
    }
    .tools {
      padding: 0px 23px;
      width: 14px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
    }
  }
  .main {
    display: flex;
    // background: #324057;
    position: absolute;
    top: 60px;
    bottom: 0px;
    overflow: hidden;
    aside {
      flex: 0 0 230px;
      width: 230px;
      // position: absolute;
      // top: 0px;
      // bottom: 0px;
      .el-menu {
        height: 100%;
      }
      .collapsed {
        width: 60px;
        .item {
          position: relative;
        }
        .submenu {
          position: absolute;
          top: 0px;
          left: 60px;
          z-index: 99999;
          height: auto;
          display: none;
        }
      }
    }
    .menu-collapsed {
      flex: 0 0 60px;
      width: 60px;
    }
    .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
    }
    .content-container {
      // background: #f1f2f7;
      flex: 1;
      // position: absolute;
      // right: 0px;
      // top: 0px;
      // bottom: 0px;
      // left: 230px;
      overflow-y: scroll;
      padding: 20px;
      .breadcrumb-container {
        //margin-bottom: 15px;
        .title {
          width: 200px;
          float: left;
          color: #475669;
        }
        .breadcrumb-inner {
          float: right;
        }
      }
      .content-wrapper {
        background-color: #fff;
        box-sizing: border-box;
      }
    }
  }
}
</style>



