import babelpolyfill from "babel-polyfill";
import Vue from "vue";
import App from "./App";
import VueRouter from "vue-router";
import store from "./vuex/store";
import Vuex from "vuex";
import routes from "./routes";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
//import './assets/theme/theme-green/index.css'
import Api from "./api/api";
import "font-awesome/css/font-awesome.min.css";
import "./styles/vars.scss";
import Home from "./views/Home.vue";

Vue.prototype.$api = Api;

Vue.prototype.baseURL =
    process.env.NODE_ENV === "development" ?
    "http://10.30.4.9:8080/mfp-mng-demo/" :
    "/mfp-mng-demo/";
Vue.use(VueRouter);
Vue.use(Vuex);
Vue.use(ElementUI);

import * as filters from "./plugin/filter.js";
Object.keys(filters).forEach((key) => {
    Vue.filter(key, filters[key]); //插入过滤器名和对应方法
});

const router = new VueRouter({
    mode: "hash",
    scrollBehavior(to, from, savePosition) {
        // to 要进入的目标路由对象
        // from 离开的路由对象
        // savePosition 记录滚动条的坐标
        if (savePosition) {
            return savePosition;
        } else {
            return {
                x: 0,
                y: 0,
            };
        }
    },
    routes,
});
// 页面刷新处理
router.beforeEach((to, from, next) => {
    let loadData = JSON.parse(sessionStorage.getItem("route"));
    if (!loadData) {
        next();
        return;
    }
    console.log(router.options.routes.length == 8);
    if (router.options.routes.length == 8) {
        var arr = [];
        var r = require.context("./views/pages/", true, /\.vue$/);
        let list = router.options.routes.filter((item) => {
            return item.path == loadData[0].path;
        });
        if (list.length > 0) return;
        loadData[0].path;
        loadData.forEach((item, i) => {
            arr.push({
                path: "/",
                name: item.menu_name,
                component: Home,
                iconCls: item.icon,
                children: [],
                hidden: item.super_code ? true : false,
            });
            r.keys().forEach((key) => {
                var _keyarr = key.split(".");
                _keyarr = _keyarr[1].split("/");

                if (item.super_code) {
                    loadData.forEach((item2, index) => {
                        if (item.super_code == item2.menu_code) {
                            console.log(item.menu_path);
                            if (_keyarr[2] == item.menu_path) {
                                arr[index].children.push({
                                    path: "/" + _keyarr[2],
                                    component: r(key),
                                    name: item.menu_name,
                                    iconCls: item.icon,
                                });
                                return;
                            }
                        }
                        return;
                    });
                } else {
                    if (_keyarr[2] == item.menu_path) {
                        arr[i].name = "";
                        arr[i].leaf = true;
                        arr[i].children.push({
                            path: "/" + _keyarr[2],
                            component: r(key),
                            name: item.menu_name,
                            iconCls: item.icon,
                        });
                    }
                }
            });
        });
        arr.forEach((item) => {
            router.options.routes.push(item); // 将请求的路由先存放到options对象中
        });
        router.addRoutes(arr);
        next({
            path: to.redirectedFrom,
        });
    } else {
        next();
    }
});

new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount("#app");


