<template>
  <el-table
    :data="data"
    @select="handleSelectionChange"
    @select-all="handleSelectAll"
    element-loading-text="Loading"
    border
    fit
    highlight-current-row
  >
    <slot name="begin"> </slot>
    <slot name="head">
      <el-table-column align="center" label="序号" width="50">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column></slot
    >
    <el-table-column
      v-for="item in list"
      :key="item.prop"
      :label="item.label"
      :prop="item.prop"
      :sortable="item.sort"
      :min-width="item.width"
      align="center"
    >
    </el-table-column>
    <slot></slot>
  </el-table>
</template>
<script>
export default {
  name: "Table",
  props: {
    list: {
      type: Array,
      default: [],
    },
    data: {
      type: Array,
      default: [],
    },
  },
  methods: {
    handleSelectionChange(list) {
      this.$emit("select", list);
    },
    handleSelectAll(list) {
      this.$emit("select", list);
    },
  },
};
</script>



