// 银行卡过滤
export function cardFilter(num) {
    if (!num) return '-'
    let reg = /^(\d{4})\d+(\d{4})$/;
    let card = num.replace(reg, "$1******$2");
    return card;
}
// 时间处理
export function timeFilter(time) {
    var date =
        time.substring(0, 4) +
        "-" +
        time.substring(4, 6) +
        "-" +
        time.substring(6, 8) +
        " " +
        time.substring(8, 10) +
        ":" +
        time.substring(10, 12) +
        ":" +
        time.substring(12, 14);
    return date;
}
// 支付类型
export function cstFilter(flag) {
    const statusMap = {
        "0": "建行钱包",
        "1": "龙支付",
        "3": "银行卡",
        "4": "银行卡",
        "5": "银行卡",
        "6": "熊猫币",
    }
    return statusMap[flag] || "-";
}


