<template>
  <div>
    <el-button
      type="primary"
      size="small"
      @click="visible = true"
      style="margin-bottom: 10px"
      >新增角色</el-button
    >
    <base-table :list="dataList" :data="roleList">
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            type="text"
            icon="el-icon-edit-outline"
            @click="editRole(scope.row.role_id)"
            >编辑</el-button
          >
          <el-button
            type="text"
            icon="el-icon-delete"
            style="color: red"
            v-if="scope.row.role_id > 2020080400000007"
            @click="deleteRole(scope.row.role_id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </base-table>
    <el-dialog
      :title="form.role_id ? '修改角色' : '新增角色'"
      :visible.sync="visible"
      width="45%"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="90px">
        <el-form-item label="角色名称" prop="role_name">
          <el-input size="small" v-model="form.role_name"></el-input>
        </el-form-item>
        <el-form-item label="角色类型" prop="role_type">
          <el-radio v-model="form.role_type" :label="1">普通用户</el-radio>
          <!-- <el-radio v-model="form.role_type" label="2">商户</el-radio> -->
        </el-form-item>
        <el-form-item label="菜单权限" prop="menuids">
          <div style="overflow-y: scroll; height: 200px; width: 100%">
            <el-tree
              ref="tree"
              :data="menuList"
              show-checkbox
              node-key="menu_code"
              icon-class="el-icon-s-flag"
              :default-checked-keys="[5]"
              :props="defaultProps"
              default-expand-all
              check-on-click-node
            >
            </el-tree>
          </div>
        </el-form-item>
        <el-form-item label="角色描述" prop="role_desc">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder="请输入内容"
            v-model="form.role_desc"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="text-align: center">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="addRole"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import baseTable from "../../components/Table";

export default {
  name: "admin",
  components: {
    baseTable,
  },
  data() {
    return {
      defaultProps: {
        label: "menu_name",
        children: "sub_tree",
      },
      form: {
        menuids: [],
      },
      dataList: [
        {
          label: "角色名称",
          prop: "role_name",
        },
        {
          label: "角色类型",
          prop: "role_type",
        },
        {
          label: "角色id",
          prop: "role_id",
        },
        {
          label: "角色描述",
          prop: "role_desc",
        },
      ],
      rules: {
        role_name: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
        ],
        role_type: [
          { required: true, message: "请输入角色类型", trigger: "change" },
        ],
        // menuIds: [
        //   {
        //     type: "array",
        //     required: true,
        //     message: "请选择角色权限",
        //     trigger: "change",
        //   },
        // ],
        firstMenu: [],
      },
      menuList: [],
      roleList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      visible: false,
    };
  },
  mounted() {
    this.getRoleList();
    this.getMenuList();
  },
  watch: {
    visible(val) {
      if (!val) {
        this.form = { menuids: [] };
      }
    },
  },
  methods: {
    // 获取所有菜单
    getMenuList() {
      this.$api.getMenu().then((res) => {
        this.menuList = res.data.menu_list;
        this.firstMenu = res.data.menu_list
          .filter((item) => {
            return item.menu_path == "/";
          })
          .map((item) => {
            return item.menu_code;
          });
        console.log(this.firstMenu);
      });
    },
    // 新增角色/更新角色
    addRole() {
      this.form.menuids = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedKeys(),
      ];
      if (this.form.menuids.length == 0) {
        this.$message.error("请选择角色权限");
        return;
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.role_id) {
            this.$api.updateRole(this.form).then((res) => {
              this.handleClose();
              this.getRoleList();
            });
          } else {
            this.$api.addRole(this.form).then((res) => {
              this.handleClose();
              this.getRoleList();
            });
          }
        }
      });
    },
    // 编辑角色
    editRole(role_id) {
      this.$api
        .getRoleInfo({
          role_id,
        })
        .then((res) => {
          console.log(res);
          this.visible = true;
          this.form = res.data.role_msg[0];
          this.$nextTick(() => {
            let arr = res.data.menu_code.filter((item) => {
              return !this.firstMenu.some((el) => el === item);
            });
            this.$refs.tree.setCheckedKeys(arr);
          });
        });
    },
    // 获取角色列表
    getRoleList() {
      this.$api
        .getRoleList({
          page: this.pageNum - 1,
          number: this.pageSize,
        })
        .then((res) => {
          res.data.userList.map((item) => {
            return (item.role_type = item.role_type == "2" ? "商户" : "员工");
          });
          this.roleList = res.data.userList;
          this.total = res.data.total;
          this.form = { menuids: [] };
        });
    },
    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getRoleList();
    },
    // 跳转页码
    changePage(page) {
      this.page = page;
      this.getRoleList();
    },
    // 删除角色
    deleteRole(role_id) {
      this.$confirm("确定删除该角色吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      })
        .then(() => {
          this.$api
            .deleteRole({
              role_id: role_id,
            })
            .then((res) => {
              console.log(res);
              this.getRoleList();
            });
        })
        .catch(() => {
          this.delList = [];
        });
    },

    handleClose() {
      this.visible = false;
      this.form = { menuids: [] };
      this.$refs.tree.setCheckedKeys([]);
    },
  },
};
</script>



