import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'


Vue.use(Vuex)

// 应用初始状态
const state = {
    count: 10,
    bankList: [], // 机构分行
    typeList: [], // 商户类型列表
    name:'1'  //个人中心,商户中心
}

// 定义所需的 mutations
const mutations = {
    getBank(state, data) {
        state.bankList = data
    },
    getBusType(state, data) {
        state.typeList = data
    },
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
    changeName(state,data){
     state.name=data
    }

}

// 创建 store 实例
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


