<template>
  <div>
    <!-- 商户：商户名，商户编号，商户类型，联系人，手机号 -->
    <!-- 手机号和姓名 可修改 -->
    <el-form
      ref="form"
      :model="user"
      label-width="80px"
      style="width: 480px"
      v-if="role == '4'"
    >
      <el-form-item label="商户名">
        <el-input v-model="user.shop_name" disabled></el-input>
      </el-form-item>
      <el-form-item label="商户编号">
        <el-input v-model="user.shop_id" disabled></el-input>
      </el-form-item>
      <el-form-item label="商户类型">
        <el-select v-model="user.shop_type" disabled>
          <el-option
            v-for="item in typeList"
            :key="item.type_id"
            :label="item.type_name"
            :value="item.type_id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="联系人">
        <el-input v-model="user.link_person" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="手机号">
        <el-input v-model="user.link_phone" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="所属分行">
        <el-input
          v-model="user.branch_name"
          disabled
          style="width: 400px"
        ></el-input>
      </el-form-item>
      <el-form-item inline v-if="!infoBtn">
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = true;
            }
          "
          size="small"
          >修改商户信息</el-button
        >
      </el-form-item>
      <el-form-item inline v-if="infoBtn">
        <el-button type="primary" @click="handleChange" size="small"
          >确认</el-button
        >
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = false;
            }
          "
          size="small"
          >返回</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 分行员工: 姓名、员工编号、所属分行、手机号、角色。 -->
    <!-- 手机号、登录密码、交易密码可修改 -->
    <el-form
      ref="form"
      :model="user"
      label-width="80px"
      style="width: 480px"
      v-else
    >
      <el-form-item label="姓名">
        <el-input v-model="user.name" disabled></el-input>
      </el-form-item>
      <el-form-item label="用户编号">
        <el-input v-model="user.account_id" disabled></el-input>
      </el-form-item>
      <el-form-item label="所属机构">
        <el-input
          v-model="user.branch_name"
          disabled
          style="width: 300p x"
        ></el-input>
      </el-form-item>
      <el-form-item label="手机号">
        <el-input v-model="user.phone" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="角色">
        <el-input
          v-model="user.role_name"
          disabled
          style="width: 400px"
        ></el-input>
      </el-form-item>
      <el-form-item inline v-if="!infoBtn">
        <el-button type="primary" @click="infoBtn = true" size="small"
          >修改个人信息</el-button
        >
        <el-button type="primary" @click="changePwd('loginPwd')" size="small"
          >修改登录密码</el-button
        >
        <el-button type="primary" @click="changePwd('businessPwd')" size="small"
          >修改交易密码</el-button
        >
      </el-form-item>

      <el-form-item inline v-if="infoBtn">
        <el-button type="primary" @click="handleChange()" size="small"
          >确认</el-button
        >
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = false;
            }
          "
          size="small"
          >返回</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 修改密码弹框 -->
    <el-dialog
      :title="type == 'loginPwd' ? '登录密码修改' : '交易密码修改'"
      :visible.sync="pwdDialog"
      width="30%"
      :before-close="handleClose"
    >
      <el-form
        ref="ruleForm"
        status-icon
        :model="ruleForm"
        :rules="rules"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="原密码" prop="old_pwd">
          <el-input v-model="ruleForm.old_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="new_pwd">
          <el-input v-model="ruleForm.new_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="重复新密码" prop="confirm_pwd">
          <el-input v-model="ruleForm.confirm_pwd" type="password"></el-input>
        </el-form-item>
        <div class="dialog-footer" style="text-align: center">
          <el-button
            @click="
              () => {
                this.pwdDialog = false;
              }
            "
            size="small"
            >取 消</el-button
          >
          <el-button
            type="primary"
            @click="changePwdSure('ruleForm')"
            size="small"
            >确 定</el-button
          >
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      role: "1",
      user: {},
      rules: {
        old_pwd: [
          { required: true, message: "请输入旧密码", trigger: "blur" },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        new_pwd: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        confirm_pwd: [
          { required: true, message: "请重复输入新密码", trigger: "blur" },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
      },
      ruleForm: {},
      infoBtn: false,
      pwdDialog: false,
      flag: "0",
      type: "",
    };
  },
  mounted() {
    this.role = sessionStorage.getItem("role");
    this.getMyInfo();
    this.getTypeList();
  },
  computed: {
    typeList: {
      get: function () {
        return this.$store.state.typeList;
      },
      set: function (newVal) {
        this.$store.state.typeList = newVal;
      },
    },
  },
  methods: {
    // 获取用户信息
    getMyInfo() {
      this.$api
        .getMyInfo({
          flag: this.role == "4" ? "1" : "0",
        })
        .then((res) => {
          this.user = res.data.result;
          this.flag = res.data.type;
        });
    },
    // 商户类型列表
    getTypeList() {
      if (this.typeList.length != 0) return;
      this.$api.getBusinessType().then((res) => {
        this.typeList = res.data;
      });
    },
    // 修改用户信息
    handleChange() {
      if (!this.user.phone && !this.user.link_phone) {
        this.$message({
          message: "请填写手机号",
          type: "error",
        });
        return;
      }
      let express = /^1[34578]\d{9}$/;
      if (!/^1[34578]\d{9}$/.test(this.user.phone || this.user.link_phone)) {
        this.$message({
          message: "手机号码不合法",
          type: "error",
        });
        return;
      }
      // 提交个人信息修改/商户修改
      this.$api
        .updateUserInfo({
          flag: this.flag,
          phone: this.user.phone,
          link_phone: this.user.link_phone,
          link_person: this.user.link_person,
        })
        .then((res) => {
          console.log(res.data);
          this.infoBtn = false;
          this.$message.success("修改号码成功");
          this.getMyInfo();
        });
    },
    // 退出修改登录密码 或 交易密码弹框
    changePwd(type) {
      this.pwdDialog = true;
      this.type = type;
      console.log("type: ", type);
    },
    // 确认修改密码
    changePwdSure(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.ruleForm.new_pwd === this.ruleForm.confirm_pwd) {
            if (this.type == "loginPwd") {
              this.$api.updateLoginPwd(this.ruleForm).then((res) => {
                console.log(res.data);
                this.pwdDialog = false;
                this.ruleForm = {};
                this.$message.success("修改登录密码成功");
              });
            } else {
              this.$api.updateBusinessPwd(this.ruleForm).then((res) => {
                console.log(res.data);
                this.pwdDialog = false;
                this.ruleForm = {};
                this.$message.success("修改交易密码成功");
              });
            }
          } else {
            this.$message({
              message: "两次密码输入不一致",
              type: "error",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleClose() {
      this.pwdDialog = false;
      this.ruleForm = {};
    },
  },
};
</script>

<style></style>



