<template>
  <div style="margin-top: 15px; text-align: center;">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "basePage",
  props: {
    page: {
      type: Number,
      default: 1,
    },
    size: {
      type: Number,
      default: 10,
    },
    total: {
      type: Number,
      default: 0,
    },
  },
  methods: {
    handleSizeChange(size) {
      this.$emit("size", size);
      // this.size =size
    },
    handleCurrentChange(page) {
      this.$emit("page", page);
      // this.page =page
    },
    // prevPage(page) {
    //   this.$emit("page", page);
    // },
    // nextPage(page) {
    //   this.$emit("page", page);
    // },
  },
};
</script>



