<template>
  <div>
    <!-- 导入Excel -->
    <div style="display: flex; justify-content: space-around">
      <el-upload
        action="/上传文件的接口"
        :on-change="onChange"
        :auto-upload="false"
        :show-file-list="false"
        accept=".xls, .xlsx"
      >
        <el-button type="warning" icon="el-icon-folder-add">批量导入</el-button>
        <!-- <div slot="tip" v-if="fileData">{{ fileData.name }}</div> -->
      </el-upload>
      <el-button type="warning" icon="el-icon-download" @click="exportToExcel"
        >批量导出</el-button
      >
    </div>

    <el-table fit :data="tableData" style="width: 100%">
      <el-table-column prop="id" label="id"> </el-table-column>
      <el-table-column prop="stuName" label="姓名"> </el-table-column>
      <el-table-column prop="stuNumber" label="学号"> </el-table-column>
      <el-table-column prop="sex" label="性别"> </el-table-column>
      <el-table-column prop="xueyuan" label="学院"> </el-table-column>
      <el-table-column prop="banji" label="班级"> </el-table-column>
      <el-table-column prop="sushe" label="宿舍号"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
import XLSX from "xlsx";

export default {
  data() {
    return {
      tableData: [],
    };
  },
  methods: {
    // 文件选择回调
    onChange(file, fileList) {
      console.log(fileList);
      this.fileData = file; // 保存当前选择文件
      this.readExcel(); // 调用读取数据的方法
    },
    // 读取数据
    readExcel(e) {
      console.log(e);
      let that = this;
      const files = that.fileData;
      console.log(files);
      if (!files) {
        //如果没有文件
        return false;
      } else if (!/\.(xls|xlsx)$/.test(files.name.toLowerCase())) {
        this.$message.error("上传格式不正确，请上传xls或者xlsx格式");
        return false;
      }
      const fileReader = new FileReader();
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result;
          // console.log(data)
          const workbook = XLSX.read(data, {
            type: "binary",
          });
          console.log(workbook);
          console.log(workbook.SheetNames);
          if (workbook.SheetNames.length >= 1) {
            this.$message({
              message: "导入数据表格成功",
              showClose: true,
              type: "success",
            });
          }
          const wsname = workbook.SheetNames[0]; //取第一张表
          const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //生成json表格内容
          console.log(ws);
          that.outputs = []; //清空接收数据
          for (var i = 0; i < ws.length; i++) {
            var sheetData = {
              // 键名为绑定 el 表格的关键字，值则是 ws[i][对应表头名]
              id: ws[i]["id"],
              stuName: ws[i]["姓名"],
              stuNumber: ws[i]["学号"],
              sex: ws[i]["性别"],
              xueyuan: ws[i]["学院"],
              banji: ws[i]["班级"],
              sushe: ws[i]["宿舍号"],
            };
            that.tableData.unshift(sheetData);
          }
          this.$refs.upload.value = "";
        } catch (e) {
          console.log(e);
          return false;
        }
      };
      // 如果为原生 input 则应是 files[0]
      fileReader.readAsBinaryString(files.raw);
    },
    exportToExcel() {
      //excel数据导出
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../../excel/Export2Excel");
        const tHeader = [
          "id",
          "学号",
          "姓名",
          "性别",
          "联系电话",
          "宿舍号",
          "学院",
          "班级",
          "楼宇号",
        ];
        const filterVal = [
          "id",
          "stuNumber",
          "stuName",
          "sex",
          "tel",
          "sushe",
          "xueyuan",
          "banji",
          "lyNumber",
        ];
        const list = this.tableData;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "学生列表excel");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map((v) => filterVal.map((j) => v[j]));
    },
  },
};
</script>

<style scoped></style>



