/* eslint-disable  */
import VueRouter from "vue-router";
import routes from "../routes";
import axios from "axios";
import {
    Message
} from "element-ui";
const router = new VueRouter({
    routes,
});

axios.defaults.headers.post["Content-Type"] =
    "application/octet-stream;charset=UTF-8;application/json;image/png";

// 环境切换
const ajaxUrl =
    process.env.NODE_ENV === "development" ?
    "http://10.30.4.9:8080/mfp-mng-demo/" : "/mfp-mng-demo/";
// create an axios instance
const service = axios.create({
    // baseURL: process.env.VUE_APP_BASE_API, // url = base url + request url
    withCredentials: true,
    timeout: 60000, // request timeout
});

// request interceptor
service.interceptors.request.use(
    (config) => {
        return config;
    },
    (error) => {
        // do something with request error
        console.log(error); // for debug
        return Promise.reject(error);
    }
);

// response interceptor
service.interceptors.response.use(
    (response) => {
        const responseCode = response.status;
        // 状态码处理
        if (responseCode == 200) {
            return Promise.resolve(response);
        } else {
            Message({
                message: response.message || "Error",
                type: "error",
                duration: 5 * 1000,
            });
            console.log(response);
            return Promise.reject(new Error(response || "Error"));
        }
    },
    (error) => {
        Message.close();
        if (error.response) {
            const responseCode = error.response.status;
            switch (responseCode) {
                case 471:
                case 472:
                    Message.error(error.response.data.tips.message);
                    sessionStorage.removeItem("user");
                    sessionStorage.removeItem("route");
                    const router = new VueRouter({
                        routes,
                    });
                    router.push("/login");
                    location.reload();
                    break;
                case 404:
                    Message.error("Network Error!!");
                    break;
                default:
                    Message.error(error.response.data.tips.message);
                    break;
            }
            return Promise.reject(error.response);
        } else {
            console.log("Please check your internet connection.");
            Message.error({
                message: "网络异常",
            });
        }
    }
);

// 封装get和post方法
export function get(path, params) {
    return new Promise((resolve, reject) => {
        service
            .get(ajaxUrl + path, {
                params: params,
            })
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                reject(err.data);
            });
    });
}
export function post(path, code, params) {
    return new Promise((resolve, reject) => {
        service
            .post(ajaxUrl + path + "?txcode=" + code, params)
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                reject(err);
            });
    });
}
// 下载文件
export function postFile(code, params) {
    let formData = new FormData();
    for (var i in params) {
        formData.append(i, params[i]);
    }
    let xhr = new XMLHttpRequest();
    xhr.open("post", ajaxUrl + "dofile?txcode=" + code, true);
    xhr.send(formData);
    xhr.responseType = "blob";
    return new Promise((resolve, reject) => {
        xhr.onload = function () {
            var blob = this.response;
            if (this.status == 200) {
                var aTag = document.createElement("a");
                let result = window.URL.createObjectURL(blob);
                if (params.flag == "1") {
                    aTag.download = "导入文件.xlsx";
                } else {
                    aTag.download = "操作结果.xlsx";
                }
                aTag.href = result;
                aTag.click();
            } else {
                resolve(blob);
            }
        };
    });
}
// 上传文件
export function uploadFile(path, code, params) {
    let formData = new FormData();
    for (var i in params) {
        formData.append(i, params[i]);
    }
    return new Promise((resolve, reject) => {
        service
            .post(ajaxUrl + path + "?txcode=" + code, formData)
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                reject(err);
                console.log(err);
            });
    });
    // let xhr = new XMLHttpRequest();
    // xhr.open("post", ajaxUrl + path + "?txcode=" + code, true);
    // xhr.send(formData);
    // return new Promise((resolve, reject) => {
    //     xhr.onload = function() {
    //         if (this.readyState == 4 && this.status == 200) {
    //             let obj = JSON.parse(this.response);
    //             resolve(obj);
    //         }
    //     }
    // });
}


