<template>
  <el-form
    :model="form"
    :rules="rules2"
    ref="form"
    label-position="left"
    label-width="0px"
    class="demo-ruleForm login-container"
  >
    <h3 class="title">MFP管理后台</h3>
    <!-- <el-radio-group
      v-model="form.flag"
      style="width: 100%; margin-bottom: 20px"
    >
      <el-radio-button label="1" style="width: 50%"
        >分行员工登录</el-radio-button
      >
      <el-radio-button label="2" style="width: 50%">商户登录</el-radio-button>
    </el-radio-group> -->
    <div class="loginTitle">
      <h2>登录</h2>
    </div>
    <template>
      <el-form-item prop="userid">
        <el-input
          type="text"
          v-model="form.userid"
          prefix-icon="el-icon-s-custom"
          placeholder="请输入员工编号"
        ></el-input>
      </el-form-item>
      <el-form-item prop="entry_pwd">
        <el-input
          type="password"
          v-model="form.entry_pwd"
          auto-complete="off"
          show-password
          prefix-icon="el-icon-lock"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item prop="img_flowid" v-if="count > 2">
        <el-row style="width: 100%">
          <el-col :span="15"
            ><el-input
              type="text"
              v-model="form.img_flowid"
              auto-complete="off"
              placeholder="请输入验证码"
            ></el-input
          ></el-col>
          <el-col :span="8" :offset="1"
            ><img
              id="img"
              :src="this.baseURL + 'code?txcode=code001'"
              @click="getCode"
          /></el-col>
        </el-row>
      </el-form-item>
    </template>
    <!-- <template v-else>
      <el-form-item prop="phone">
        <el-input
          type="text"
          v-model="form.phone"
          prefix-icon="el-icon-s-custom"
          placeholder="请输入手机号码"
        ></el-input>
      </el-form-item>
      <el-form-item prop="phone_pass">
        <el-row style="width: 100%">
          <el-col :span="15"
            ><el-input
              type="text"
              v-model="form.phone_pass"
              auto-complete="off"
              prefix-icon="el-icon-lock"
              placeholder="请输入短信验证码"
            ></el-input
          ></el-col>
          <el-col :span="8" :offset="1"
            ><el-button
              :type="sendFlag ? 'info' : 'primary'"
              size="medium"
              @click="getPhoneCode"
              >{{ sendFlag ? time + "s重新发送" : "获取验证码" }}</el-button
            ></el-col
          >
        </el-row>
      </el-form-item>
    </template> -->
    <div style="text-align: right">
      <el-button type="text" @click="forgetPass">{{
        form.flag == "1" ? "忘记密码" : "收不到验证码"
      }}</el-button>
    </div>
    <el-form-item style="width: 100%">
      <el-button
        type="primary"
        style="width: 100%"
        @click.native.prevent="submit"
        :loading="logining"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import Home from "./Home.vue";
import Main from "./Main.vue";
//import NProgress from 'nprogress'
export default {
  data() {
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("联系电话不能为空"));
      }
      var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
      if (phoneReg.test(value)) {
        callback();
      } else {
        return callback(new Error("手机号码非法"));
      }
    };
    return {
      logining: false,
      form: {
        flag: "1",
      },
      rules2: {
        userid: [{ required: true, message: "请输入账号", trigger: "blur" }],
        entry_pwd: [{ required: true, message: "请输入密码", trigger: "blur" }],
        phone: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" },
        ],
        phone_pass: [
          { required: true, message: "请输入手机验证码", trigger: "blur" },
        ],
      },
      count: 0,
      src: "",
      sendFlag: false,
      time: 60,
    };
  },
  watch: {
    count(num) {
      if (num >= 3) {
        this.getCode();
      }
    },
  },
  methods: {
    // 获取图形验证码
    getCode() {
      var date = new Date().getTime(); // 防止缓存
      var change = document.getElementById("img");
      change.src = this.baseURL + `code?txcode=code001&${date}`;
    },
    getPhoneCode() {
      console.log(this.form.phone);
      if (this.sendFlag) return;
      var phoneReg = /^1[3456789]\d{9}$/; // 手机号码正则
      if (!phoneReg.test(this.form.phone)) {
        return;
      }
      this.$api
        .getPhoneCode({
          mobile: this.form.phone,
        })
        .then((res) => {
          this.sendFlag = true;
          var loop = setInterval(() => {
            this.time--;
            if (this.time == 0) {
              this.time = 59;
              this.sendFlag = false;
              clearInterval(loop);
            }
          }, 1000);
        });
    },
    // 提交
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.logining = true;
          setTimeout(() => {
            this.logining = false;
          }, 2000);
          if (this.form.flag == "2") {
            this.form.userid = this.form.phone;
            this.form.entry_pwd = this.form.phone_pass;
          }
          this.$api
            .login(this.form)
            .then((res) => {
              this.count = 0;
              this.logining = false;
              sessionStorage.setItem("user", JSON.stringify(res.userMap));
              sessionStorage.setItem("role", res.userRole);
              sessionStorage.setItem("role_id", res.role_id);
              sessionStorage.setItem("route", JSON.stringify(res.menuList));

              if (res.userRole === "4") {
                this.$store.commit("changeName", "4");
              } else {
                this.$store.commit("changeName", "1");
              }

              var loadData = res.menuList;
              var arr = [];
              var r = require.context("./pages/", true, /\.vue$/);
              loadData.forEach((item, i) => {
                arr.push({
                  path: "/",
                  name: item.menu_name,
                  component: Home,
                  iconCls: item.icon,
                  children: [],
                  hidden: item.super_code ? true : false,
                });
                r.keys().forEach((key) => {
                  var _keyarr = key.split(".");
                  _keyarr = _keyarr[1].split("/");

                  if (item.super_code) {
                    loadData.forEach((item2, index) => {
                      if (item.super_code == item2.menu_code) {
                        console.log(item.menu_path);
                        if (_keyarr[2] == item.menu_path) {
                          arr[index].children.push({
                            path: "/" + _keyarr[2],
                            component: r(key),
                            name: item.menu_name,
                            iconCls: item.icon,
                          });
                          return;
                        }
                      }
                      return;
                    });
                  } else {
                    if (_keyarr[2] == item.menu_path) {
                      arr[i].name = "";
                      arr[i].leaf = true;
                      arr[i].children.push({
                        path: "/" + _keyarr[2],
                        component: r(key),
                        name: item.menu_name,
                        iconCls: item.icon,
                      });
                    }
                  }
                });
              });
              arr.forEach((item) => {
                this.$router.options.routes.push(item); // 将请求的路由先存放到options对象中
              });
              this.$router.addRoutes(arr);
              this.$router.push(
                arr[0].children ? arr[0].children[0].path : arr[0].path
              );
            })
            .catch((err) => {
              console.log(err);
              // if (
              //   err.data.tips.message ==
              //   "密码错误次数超过3次,需开启图形验证码验证"
              // ) {
              //   this.count = 4;
              // }
              // this.count = this.count + 1;
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 忘记密码/收不到验证码
    forgetPass() {
      this.$alert("请联系分行管理员重置密码", "忘记密码", {
        confirmButtonText: "确定",
        center: true,
        callback: (action) => {
          console.log("弹出");
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.login-container {
  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .loginTitle {
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}

img {
  height: 40px;
  // margin-left: 10%;
}
</style>



